import styled from 'styled-components';
import { Link as LinkR } from 'react-router-dom';
import { Link as LinkS } from 'react-scroll';
import MenuIcon from '@material-ui/icons/Menu';
import { useEffect, useState } from 'react';

const Nav = styled.nav`
    background: ${({ scrollNav }) => (scrollNav ? 'black' : 'transparent')};
    height: 80px;
    margin-top: -80px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 10;
    transition: all 0.8s;
`
const NavbarContainer = styled.div`
    display: flex;
    width: 100%;
    justify-content: space-between;
    height: 80px;
    z-index: 1;
    padding: 0 24px;
    max-width: 1100px;
`
const NavLogo = styled(LinkS)`
    color: white;
    justify-self: flex-start;
    cursor: pointer;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    margin-left: 24px;
    font-weight: bold;
    text-decoration: none;
`
const MobileIcon = styled.div`
    display: none;
    @media screen and (max-width: 768px) {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-100%, 50%);
        font-size: 1.8rem;
        cursor: pointer;
        color: white;
        >svg {
            font-size: inherit;  
        }
    }
`
const NavMenu = styled.ul`
    display: flex;
    align-items: center;
    list-style: none;
    text-align: center;
    margin-right: -22px;
    @media screen and (max-width: 768px) {
        display: none;
    }
`
const NavItem = styled.li`
    height: 80px;
    position: relative;
    overflow: hidden;
    &::after {
        content: '';
        position: absolute;
        width: 100%;
        height: 3px;
        background-color: #01bf71;
        bottom: 0;
        left: -100%;
        z-index: 2;
        transition: .5s ease;
    }
    &:hover::after {
        left: 0;
    }
`
const NavLinks = styled(LinkS)`
    color: white;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0 1rem;
    height: 100%;
    cursor: pointer;
    &.active {
        border-bottom: 3px solid #01bf71;
    }
`
const NavBtn = styled.nav`
    display: flex;
    align-items: center;
    @media screen and (max-width: 768px) {
        display: none;
    }
`
const NavBtnLink = styled(LinkR)`
    border-radius: 50px;
    background-color: #01bf71;
    white-space: nowrap;
    padding: 10px 22px;
    color: #010606;
    border: none;
    cursor: pointer;
    transition: all 0.2s;
    text-decoration: none;
    &:hover {
        background-color: white;
        color: #010606;
    }
`

export default function Navbar({ toggle }) {
    const [scrollNav, setScrollNav] = useState(false);

    const changeNav = () => {
        if (window.scrollY >= 80) {
            setScrollNav(true);
        } else {
            setScrollNav(false);
        }
    }

    useEffect(() => {
        window.addEventListener('scroll', changeNav);
        return () => {
            window.removeEventListener('scroll', changeNav);
        };
    }, []);


    return (
        <>
            <Nav scrollNav={scrollNav}>
                <NavbarContainer>
                    <NavLogo smooth={true} duration={500} spy={true} exact='true' offset={-80} to='home'>
                        dolla
                    </NavLogo>
                    <MobileIcon onClick={toggle}>
                        <MenuIcon />
                    </MobileIcon>
                    <NavMenu>
                        <NavItem>
                            <NavLinks smooth={true} duration={500} spy={true} exact='true' offset={-80} to="about" > About</NavLinks>
                        </NavItem>
                        <NavItem>
                            <NavLinks smooth={true} duration={500} spy={true} exact='true' offset={-80} to="discover">Discover</NavLinks>
                        </NavItem>
                        <NavItem>
                            <NavLinks smooth={true} duration={500} spy={true} exact='true' offset={-80} to="services">Services</NavLinks>
                        </NavItem>
                        <NavItem>
                            <NavLinks smooth={true} duration={500} spy={true} exact='true' offset={-80} to="signup">Sign Up</NavLinks>
                        </NavItem>
                    </NavMenu>
                    <NavBtn>
                        <NavBtnLink to='/login'>Sign In</NavBtnLink>
                    </NavBtn>
                </NavbarContainer>
            </Nav>
        </>
    )
}
